package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.asRClass
import com.jonnyzzz.logo.web.reducers.ScriptAddAction
import com.jonnyzzz.logo.web.reducers.ScriptClearAllAction
import com.jonnyzzz.logo.web.reducers.ScriptResetAllAction
import kotlinx.css.*
import kotlinx.html.js.onClickFunction
import react.*
import react.dom.form
import react.redux.rConnect
import redux.RAction
import redux.WrapperAction
import styled.*

interface ScriptAddFormPublicProps : RProps {
  var defaultText: String?
}

private interface ScriptAddFormInnerProps : RProps, ScriptAddFormPublicProps {
  var onSubmit: (String) -> Unit
  var onReset: () -> Unit
  var onClear: () -> Unit
}

val ScriptAddFormComponent: RClass<ScriptAddFormPublicProps> =
        rConnect<RAction, WrapperAction, ScriptAddFormPublicProps, ScriptAddFormInnerProps>(
                mapDispatchToProps = { dispatch, _ ->
                  onSubmit = { dispatch(ScriptAddAction(it)) }
                  onReset = { dispatch(ScriptResetAllAction()) }
                  onClear = { dispatch(ScriptClearAllAction()) }
                }
        ).invoke(ScriptAddForm::class.asRClass())


private interface ScriptAddFormState : RState {
  var text: String
}

private class ScriptAddForm(props: ScriptAddFormInnerProps) : RComponent<ScriptAddFormInnerProps, ScriptAddFormState>(props) {
  override fun ScriptAddFormState.init() {
    setState {
      text = props.defaultText ?: ""
    }
  }

  override fun RBuilder.render() {
    form {
      styledDiv {
        css {
          verticalAlign = VerticalAlign.bottom
          children {
            float = Float.left
            marginRight = 25.px
          }
        }

        ScriptEditorComponent {
          attrs.text = state.text
          attrs.onTextChange = { setState { text = it }}
        }

        styledButton {
          css {
            height = 50.px
            width = 150.px
            display = Display.block
          }

          attrs.onClickFunction = {
            it.preventDefault()
            val text = state.text
            if (text.isNotBlank()) {
              props.onSubmit(text)
              setState { this.text = props.defaultText ?: "" }
            }
          }

          +"Run Block"
        }

        styledA(href = "#") {
          css {
            display = Display.block
          }

          attrs.onClickFunction = {
            it.preventDefault()
            props.onReset()
          }

          +"Reset"
        }

        styledA(href = "#") {
          css {
            display = Display.block
          }

          attrs.onClickFunction = {
            it.preventDefault()
            props.onClear()
          }

          +"Clear"
        }
      }
    }
  }
}
