package com.jonnyzzz.logo.web.script

fun processScript(text: String): List<ScriptCommand> {
  return text.trim().split("\n")
          .map { it.trim().split("#")[0] }
          .filter { it.isNotBlank() }
          .flatMap {
            runOneCommand(it)
          }
}

private fun runOneCommand(command: String) : List<ScriptCommand> {
  return when {
    command.startsWith("fd ", ignoreCase = true) -> {
      val sz = command.removePrefix("fd").trim().toIntOrNull() ?: return listOf()
      listOf(ScriptMoveCommand(sz))
    }

    command.startsWith("lt ", ignoreCase = true) -> {
      val angle = command.removePrefix("lt").trim().toIntOrNull() ?: return listOf()
      listOf(ScriptTurnCommand(-angle))
    }

    command.startsWith("rt ", ignoreCase = true) -> {
      val angle = command.removePrefix("rt").trim().toIntOrNull() ?: return listOf()
      listOf(ScriptTurnCommand(angle))
    }

    command.equals("pu", ignoreCase = true) -> {
      listOf(ScriptPenUpCommand)
    }

    command.equals("pd", ignoreCase = true) -> {
      listOf(ScriptPenDownCommand)
    }

    command.equals("home", ignoreCase = true) -> {
      listOf(ScriptJumpHome)
    }

    command.startsWith("arc ", ignoreCase = true) -> {
      val args = command
        .removePrefix("arc ")
        .trim()
        .split(" ")
        .map { it.trim() }
        .filter { it.isNotBlank() }
        .map { it.toIntOrNull() ?: return listOf() }

      if (args.size != 2) return listOf()

      listOf(ScriptArcCommand(args[0], args[1]))
    }

    else -> listOf()
  }
}
