package com.jonnyzzz.logo.web

import com.jonnyzzz.logo.web.components.*
import com.jonnyzzz.logo.web.css.JogoStyle
import com.jonnyzzz.logo.web.i18.Messages
import com.jonnyzzz.logo.web.script.ScriptWorld
import kotlinx.css.*
import react.RBuilder
import react.dom.div
import react.dom.h1
import styled.css
import styled.styledDiv
import styled.styledPre

fun RBuilder.studio() {
  h1 {
    + Messages.Titles.jogo
  }

  twoColumnsWithBorder(
          { SvgWorldComponent { attrs.world = ScriptWorld(300, 300) } },
          { ScriptsViewComponent {} }
  )

  ScriptAddFormComponent {
    attrs.defaultText = ""
  }

  styledDiv {
    css {
      clear = Clear.both
      height = 25.px
    }
  }

  div {
    + "There following commands are possible:"
    styledPre {
      JogoStyle.jetbrainsMono(this)
      +"fd 20\nlt 90\nfd 20\nlt 90\nfd 20"
    }
  }
}
