package com.jonnyzzz.logo.web.css

import kotlinx.css.*
import styled.StyledBuilder

object JogoStyle {
  fun html(builder: StyledBuilder<*>) = builder.css.apply {
    paddingAll = 15.px
  }

  fun jetbrainsMono(builder: StyledBuilder<*>) = jetbrainsMono(builder.css)

  fun jetbrainsMono(builder: CSSBuilder) = builder.apply {
    fontFamily = "'JetBrains Mono', monospace"
  }
}


private var CSSBuilder.paddingAll: LinearDimension
  get() = error("Not implemented")
  set(value) {
    paddingLeft = value
    paddingRight = value
    paddingBottom = value
    paddingTop = value
  }
