package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.asRClass
import com.jonnyzzz.logo.web.css.JogoStyle
import kotlinx.html.js.onChangeFunction
import org.w3c.dom.HTMLTextAreaElement
import react.*
import react.dom.value
import styled.styledDiv
import styled.styledTextArea

val ScriptEditorComponent = ScriptEditor::class.asRClass()

interface ScriptEditorProps : RProps {
  var text: String
  var onTextChange : (String) -> Unit
}

class ScriptEditor : RComponent<ScriptEditorProps, RState>() {
  private val textAreaRef = createRef<HTMLTextAreaElement>()
  private var currentView : dynamic = null

  override fun RBuilder.render() {
    styledDiv {
      styledTextArea {
        JogoStyle.jetbrainsMono(this)

        attrs.readonly = false
        attrs.rows = "5"
        attrs.cols = "80"
        attrs.value = props.text
        ref = textAreaRef

        attrs.onChangeFunction = {
          props.onTextChange(it.target.unsafeCast<HTMLTextAreaElement>().value)
        }
      }
    }
  }

  override fun componentDidMount() {
//    currentView = externalCodeMirrorObject.fromTextArea(
//      textAreaRef.current,
//      js {
//        smartIndent = true
//      }
//    )
  }

  override fun componentDidUpdate(prevProps: ScriptEditorProps, prevState: RState, snapshot: Any) {
    //TODO()
  }

  override fun componentWillUnmount() {
    currentView?.destroy()
    currentView = null
  }
}
